import React, { useState } from "react";
import { NavLink, withRouter } from "react-router-dom";
import MenuRoundedIcon from "@material-ui/icons/MenuRounded";
import MenuOpenRoundedIcon from "@material-ui/icons/MenuOpenRounded";
import { green } from "@material-ui/core/colors";
import { retry } from "async";
import Slide from "@material-ui/core/Slide";

function Navbar({ user, setUser, history }) {
  const [showMenu, setShowMenu] = useState(false);

  function handleLogout() {
    localStorage.removeItem("token");
    setUser({});
    history.push("/");
  }

  function menuClick(event) {
    event.preventDefault;
    console.log("menu icon clicked");
    setShowMenu(!showMenu);
  }

  function MenuOpen() {
    return (
      <div id="menuOpen">
        <div>Menu Option1</div>
        <div>Menu Option2</div>
        <div>Menu Option3</div>
        <div>Menu Option4</div>
      </div>
    );
  }

  return (
    <div>
      <h1>La Garage</h1>
      {showMenu === true ? (
        <div id="showMenu">
          <Slide direction="right" in={showMenu} mountOnEnter unmountOnExit>
            <MenuOpenRoundedIcon
              fontSize="large"
              onClick={(event) => menuClick(event)}
            />
          </Slide>
        </div>
      ) : (
        <MenuRoundedIcon
          fontSize="large"
          onClick={(event) => menuClick(event)}
        />
      )}

      {showMenu === true ? <MenuOpen /> : <></>}

      {/* <nav>
        {user.id ? (
          <div>
            <NavLink to="/Home">Home</NavLink>
            {
              <a href="#" onClick={handleLogout}>
                Log Out
              </a>
            }
          </div>
        ) : (
          <div>
            <NavLink to="/login">Login</NavLink>
            <NavLink to="/signup">Sign Up</NavLink>
          </div>
        )}
      </nav> */}
    </div>
  );
}

export default withRouter(Navbar);
